import './style.css';
import React, { Component } from 'react';
import AlimentModel from '../../models/AlimentModel';
import IdGenerator from '../../IdGenerator';

class AlimentEditor extends Component {

  constructor(props) {
    super(props);
    this.state = {
      label: props.aliment.label,
      energyPer100g: props.aliment.energyPer100g,
      energyPerUnit: props.aliment.energyPerUnit,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleSubmit(event) {
    event.preventDefault();
    let aliment = new AlimentModel(this.state.label);
    aliment.energyPer100g = this.state.energyPer100g;
    aliment.energyPerUnit = this.state.energyPerUnit;
    aliment.id = (new IdGenerator()).generateAlimentId();
    this.props.onSubmit(aliment);
  }

  handleChange(event) {
    const name = event.target.name;
    this.setState({
      [name]: event.target.value
    });
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <h2>Edit aliment</h2>
        <div className="form-group">
          <label htmlFor="AliementEditorLabel">Label</label>
          <input type="text"
            className="form-control"
            id="AliementEditorLabel"
            placeholder="His name is ..."
            name="label"
            value={this.state.label}
            onChange={this.handleChange} />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPer100g">Energy per 100g</label>
          <input type="number"
            className="form-control"
            id="AliementEditorEnergyPer100g"
            placeholder="Calories per 100g"
            name="energyPer100g"
            value={this.state.energyPer100g}
            onChange={this.handleChange} />
        </div>

        <div className="form-group">
          <label htmlFor="AliementEditorEnergyPerUnit">Energy per unit</label>
          <input type="number"
            className="form-control"
            id="AliementEditorEnergyPerUnit"
            placeholder="Calories per unit"
            name="energyPerUnit"
            value={this.state.energyPerUnit}
            onChange={this.handleChange} />
        </div>

        <button type="submit" className="btn btn-primary">Save it</button>
      </form>
    );
  }
}

export default AlimentEditor;



